.sidebar {
  background: var(--color-primary);
  height: 80px;
  width: 200px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  color: #fff;
}

.nav-menu {
  background: var(--color-primary);
  width: 200px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem 0 0.5rem 1rem;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 1.2rem;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 1rem;
  border-radius: 4px;
}

.nav-text a:hover {
  background: #58595b;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background: var(--color-primary);
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 1rem;
}

.home {
  /* display: flex;
  flex-direction: column;
  justify-content: center;

  margin-left: 8rem; */

  display: grid;
  /* place-items: center center; */
  place-content: center center;
  width: 100vw;
  height: 80vh;
}

.home h1 {
  max-width: 22ch;
  margin-bottom: 1rem;
}

.home span {
  color: var(--color-primary);
}

.home .subheading {
  font-weight: 600;
  font-size: 1.2rem;
  margin-bottom: 2rem;
  max-width: 40ch;
}

.home .btn-container > *:first-child {
  margin-right: 1.5rem;
}

@media screen and (max-width: 1000px) {
  .home {
    margin-left: 4rem;
  }
}

@media screen and (max-width: 600px) {
  .home {
    margin: 0 auto;
    padding: 0 1rem;
  }
}

a {
  text-decoration: none;
  font-weight: 600;
  padding: 1rem 2rem;
}

.btn-primary {
  background: var(--color-primary);
  color: #fff;

  border: 1px solid transparent;

  transition: background 200ms, color 200ms, border 200ms;
}

.btn-primary:hover {
  background: #fff;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
}

.btn-secondary {
  background: #fff;
  color: var(--color-secondary);

  border: 1px solid var(--color-secondary);

  transition: background 200ms, color 200ms, border 200ms;
}

.btn-secondary:hover {
  background: var(--color-secondary);
  color: #fff;
  border: 1px solid transparent;
}

.form-container {
  display: grid;
  place-content: center center;
}

.form {
  background: #fff;

  min-width: 25rem;

  width: 50vw;
  padding: 3rem 2rem;
  border-radius: 8px;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);

  display: grid;
  place-items: center start;
  grid-gap: 2rem;
  gap: 2rem;
}

.form h3 {
  font-size: 2.5rem;
  justify-self: center;
}

.form-field {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.form input {
  width: 100%;
  background: #f5f5f5;
  border: none;
  padding: 8px 5px;
  font-size: 1rem;
  border-radius: 8px;
  color: var(--color-text);
  font-family: Raleway, sans-serif;
}

.form label {
  margin-bottom: 0.2rem;
}

.form button {
  text-decoration: none;
  font-weight: 600;
  padding: 1rem 2rem;
  width: 100%;
  cursor: pointer;

  background: var(--color-primary);
  color: #fff;

  border: 1px solid transparent;

  transition: background 200ms, color 200ms, border 200ms;
}

.form button:hover {
  background: #fff;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
}

.error-message {
  display: flex;
  align-items: center;
  color: red;
  margin-top: 0.2rem;
  font-size: 0.8rem;
}

.error-message > * {
  margin-right: 0.5rem;
}


.form .form-submit {
  text-decoration: none;
  font-weight: 600;
  padding: 1rem 2rem;
  width: 100%;
  cursor: pointer;

  background: var(--color-secondary);
  color: #fff;

  border: 1px solid transparent;

  transition: background 200ms, color 200ms, border 200ms;
}

.form .form-submit:hover {
  background: #fff;
  color: var(--color-secondary);
  border: 1px solid var(--color-secondary);
}

.result {
  grid-gap: 2rem;
  gap: 2rem;
  text-align: center;
  place-items: center;
}

.result .success-icon {
  font-size: 5rem;
  color: var(--color-secondary);
  text-align: center;
}

.clients-container {
  display: grid;
  place-content: start center;
}

.clients-container h1 {
  margin: 5rem 0 3.5rem 0;
  text-align: center;
}

.clients-container li {
  list-style: none;
  margin-bottom: 0.3rem;
  cursor: pointer;
  font-size: 1.25rem;
  font-weight: 500;
  transition: color 200ms, -webkit-text-decoration 200ms;
  transition: color 200ms, text-decoration 200ms;
  transition: color 200ms, text-decoration 200ms, -webkit-text-decoration 200ms;
}

.clients-container li:hover {
  color: var(--color-primary);
  text-decoration: underline;
}

.clients-container .btn-home {
  text-align: center;
  margin-top: 2rem;
}

.modal {
  display: grid;
  place-content: center center;
  height: 100vh;
}

.modal h4 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.modal p {
  margin-bottom: 0.3rem;
}

.modal .close {
  font-size: 2rem;
  color: var(--color-primary);
  cursor: pointer;
  position: absolute;
  top: -2rem;
  right: -2rem;
}

.modal-container {
  position: relative;
  padding: 2rem;
  border-radius: 8px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

