.home {
  /* display: flex;
  flex-direction: column;
  justify-content: center;

  margin-left: 8rem; */

  display: grid;
  /* place-items: center center; */
  place-content: center center;
  width: 100vw;
  height: 80vh;
}

.home h1 {
  max-width: 22ch;
  margin-bottom: 1rem;
}

.home span {
  color: var(--color-primary);
}

.home .subheading {
  font-weight: 600;
  font-size: 1.2rem;
  margin-bottom: 2rem;
  max-width: 40ch;
}

.home .btn-container > *:first-child {
  margin-right: 1.5rem;
}

@media screen and (max-width: 1000px) {
  .home {
    margin-left: 4rem;
  }
}

@media screen and (max-width: 600px) {
  .home {
    margin: 0 auto;
    padding: 0 1rem;
  }
}
